import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { DashboardComponent } from './dashboard.component';
import { HeroesComponent } from './heroes.component';
import { HeroDetailComponent } from './hero-detail.component';

const routes: Routes = [
  { path: '', redirectTo: '/dashboard', pathMatch: 'full' },
  // { path: 'heroes', loadChildren: 'app/heroes.component#HeroesComponent' },
  { path: 'heroes', component: HeroesComponent },
  // { path: 'dashboard', loadChildren: 'app/dashboard.component#DashboardComponent' },
  { path: 'dashboard', component: DashboardComponent },
  // { path: 'detail/:id', loadChildren: 'app/hero-detail.component#HeroDetailComponent' }
  { path: 'detail/:id', component: HeroDetailComponent }
];

@NgModule({
  imports: [ RouterModule.forRoot(routes) ],
  exports: [ RouterModule ]
})
export class AppRoutingModule {}


